:root {
  font-size: 30px;
}

ul,
li {
  margin: 0;
  padding: 0;
  display: block;
}

a {
  transition: 0.3s all;
  outline: none;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

button {
  transition: 0.6s all;
  border: none;
  outline: none;
  background: none;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

input {
  outline: none;
  border-radius: 0;
}
input.error {
  color: red !important;
  border-color: red !important;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 0.9rem;
}

.overflowH {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}

::-webkit-scrollbar-track {
  background: #ddd;
}

::-webkit-scrollbar-thumb {
  background: #e39b25;
  border: 1px solid #ddd;
}

.lazy {
  opacity: 0;
}

.disabled {
  pointer-events: none;
}

.container {
  padding: 0 5.5rem;
}

.section-title {
  font-size: 4rem;
  font-weight: 500;
  line-height: 1.1;
  color: #e39b25;
}

.section-text {
  line-height: 1.6;
}

.nav {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  font-size: 1rem;
}
.nav-current {
  font-size: 1.6rem;
}
.nav-line {
  width: calc(100% - 10rem);
  height: 1px;
  background: linear-gradient(270deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
}

.arrows {
  width: 7.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.arrows > span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s all;
  cursor: pointer;
  user-select: none;
  width: 1.9rem;
  color: #fff;
}
.arrows > span:hover {
  color: #00ccff !important;
}
.arrows > span svg {
  width: 100%;
}

@keyframes pulse-btn {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.3);
  }
  70% {
    box-shadow: 0 0 0 1rem rgba(255, 121, 63, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 121, 63, 0);
  }
}
@keyframes svgPulse {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0;
  }
}
.tel-popup {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  z-index: 5;
  width: 3.2rem;
  height: 3.2rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: #e39b25;
  background: #fff;
  cursor: pointer;
  animation: pulse-btn 2s infinite;
  border: 1px solid rgba(55, 74, 156, 0.5);
}
.tel-popup svg {
  width: 1.2rem;
}
.tel-popup:hover {
  color: #00ccff;
}

.feedback {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  background: rgba(55, 74, 156, 0.6);
  display: none;
}
.feedback-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 2.1rem;
  border-radius: 1rem;
  border-bottom: 0.65rem solid #e39b25;
  background: #fff;
  width: 24rem;
}
.feedback__close {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 2.5rem;
  height: 2.5rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s all;
  cursor: pointer;
  user-select: none;
  color: #e39b25;
  background: #fff;
  border-radius: 50%;
  top: -3.5rem;
}
.feedback__close:hover {
  color: #00ccff;
}
.feedback__close svg {
  width: 1.1rem;
}
.feedback__title {
  font-weight: 600;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  color: #e39b25;
  text-align: center;
}
.feedback__text {
  font-size: 0.7rem;
  font-weight: 500;
  line-height: 150%;
  text-align: center;
}
.feedback-form {
  margin: 2.5rem 0 0.8rem;
  font-size: 0.9rem;
}
.feedback-form input {
  width: 100%;
  height: 3.5rem;
  border: 1px solid rgba(17, 17, 17, 0.1);
  margin-bottom: 0.7rem;
  border-radius: 3rem;
  text-align: center;
  color: #e39b25;
  outline: none;
}
.feedback-form input::placeholder {
  color: rgba(55, 74, 156, 0.5);
}
.feedback-form .btn {
  width: 100%;
  height: 3.5rem;
  margin-top: 0.3rem;
  border-radius: 3.5rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background: #e39b25;
}
.feedback-form .btn:hover {
  background: #00ccff;
}
.feedback__agree {
  font-size: 0.7rem;
  text-align: center;
  max-width: 13rem;
  margin: 0 auto;
}
.feedback-done {
  display: none;
}
.feedback-done .feedback__text {
  margin-bottom: 1rem;
  padding: 0 1rem;
}
.feedback__img {
  margin: 4.3rem 0 3.2rem;
  text-align: center;
}
.feedback__img img {
  display: inline-block;
  width: 12.7rem;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 7;
  background: rgba(55, 74, 156, 0.6);
  display: none;
}
.menu-content {
  width: 30rem;
  background: linear-gradient(74deg, #4b469a 0%, #803c93 100%), #e39b25;
  color: #fff;
  height: 100%;
  padding: 3.5rem 5rem;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
  transform: translateX(-100%);
  transition: 0.8s all;
  transition-delay: 0.5s;
  cursor: default;
}
.menu__close {
  display: inline-flex;
  align-items: center;
  font-size: 0.8rem;
  cursor: pointer;
  user-select: none;
}
.menu__close img {
  width: 0.7rem;
  margin-right: 0.3rem;
}
.menu__list li {
  margin-bottom: 1rem;
}
.menu__list li a {
  color: #fff;
  font-size: 1.3rem;
  text-transform: uppercase;
}
.menu__list li a:hover {
  color: #00ccff;
}
.menu__list li:last-child {
  margin-bottom: 0;
}
.menu .footer-social {
  margin-top: 0;
}
.menu.show .menu-content {
  transform: none;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 11rem;
  z-index: 5;
  transition: 0.6s all;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.consult{
  color: black !important;
}
.consult ::placeholder{
  color: black !important;
}
.header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.6s all;
  width: 8.75rem;
}
.header__logo img {
  width: 100%;
  display: block;
  border-radius: 100px;
}
.header-wrap {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.header__menu {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s all;
  cursor: pointer;
  user-select: none;
  gap: 0.85rem;
  margin-right: 1.3rem;
  color: #fff;
}
.header__menu:hover {
  color: #00ccff;
}
.header__menu img {
  width: 2.5rem;
}
.header a {
  color: #fff;
}
.header a:hover {
  color: #00ccff;
}
.header-lang {
  position: relative;
  text-transform: uppercase;
  color: #fff;
}
.header-lang:hover .header-lang__list {
  display: block;
}
.header-lang__btn {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding-bottom: 1.5rem;
  margin-bottom: -1.5rem;
  gap: 0.35rem;
}
.header-lang__btn svg {
  width: 0.4rem;
}
.header-lang__list {
  position: absolute;
  top: 1.5rem;
  padding: 0.6rem 1rem;
  background: #e39b25;
  border: 1px solid #fff;
  display: none;
}
.header-lang__list a {
  margin-bottom: 0.5rem;
  display: block;
}
.header-lang__list a:last-child {
  margin-bottom: 0;
}
.header__tel {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}
.header__tel svg {
  width: 1.25rem;
}
.header__genplan {
  height: 3rem;
  padding: 0 1rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 3rem;
  border: 1px solid #00ccff;
}
.header.scrolled {
  background: #e39b25;
  border-bottom: 1px solid #fff;
  height: 4.5rem;
}
.header.scrolled .header__logo {
  width: 5rem;
}
.header-flat .header__menu {
  color: #000;
}
.header-flat .header__menu:hover {
  color: #00ccff;
}
.header-flat .header__logo img {
  filter: none;
}
.header-flat a {
  color: #000;
}
.header-flat .header-lang {
  color: #000;
}
.header-flat .header-lang__list {
  background: #fff;
  border-color: #00ccff;
}
.header-flat.scrolled {
  background:#e39b25;
  border-color: #00ccff;
}

.main {
  padding-top: 11rem;
  color: #fff;
  background:#e39b25;
  position: relative;
  z-index: 1;
}
.main__title {
  font-weight: 600;
  font-size: 7.5rem;
  text-transform: uppercase;
  margin-bottom: 0.75rem;
}
.main__text {
  font-size: 1.9rem;
  font-family: "Miama";
}
.main__img {
  margin-top: -5rem;
  position: relative;
  z-index: -1;
}
.main__img img {
  width: 100%;
  display: block;
}

.circle-wrap {
  background: #e39b25;
  color: #fff;
  overflow: hidden;
  position: relative;
}
.circle-wrap::after {
  top: 30rem;
  width: 52rem;
  height: 54rem;
  border-radius: 50%;
  content: "";
  background: #803c93;
  filter: blur(266.799987793px);
  position: absolute;
  right: -26rem;
}

.about {
  padding-top: 6rem;
  /* background: url(../img/about/pattern.png) top right no-repeat; */
  background-size: 80%;
}
.about .container {
  position: relative;
  z-index: 1;
}
.about-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 6rem;
}
.about__title {
  font-size: 3rem;
  font-weight: 600;
}
.about .section-text {
  max-width: 40rem;
}
.about-main {
  display: flex;
  gap: 1.5rem;
}
.about__img {
  width: 100%;
}
.about__img img {
  width: 100%;
  display: block;
  border-radius: 1.5rem;
}
.about-left {
  width: 33%;
}
.about-center {
  width: 42%;
}
.about-right {
  width: 25%;
  padding-top: 1.5rem;
}
.about-wrap {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1.25rem 0;
}
.about__number {
  font-size: 4.5rem;
  flex-shrink: 0;
}
.about__text {
  font-size: 1rem;
  line-height: 1.35;
}
.about__text div {
  font-weight: 600;
}

.consult {
  padding-top: 6.5rem;
  position: relative;
  z-index: 1;
}
.consult-wrap {
  max-width: 54rem;
  margin: 0 auto;
  text-align: center;
}
.consult__title {
  font-size: 2.5rem;
  font-weight: 400;
  margin-bottom: 2.5rem;
}
.consult-form {
  display: flex;
  align-items: center;
  gap: 1.5rem 3rem;
  flex-wrap: wrap;
  font-size: 0.95rem;
}
.consult-form > input {
  width: calc(38% - 2rem);
  border: none;
  background: transparent;
  border-bottom: 1px solid #bfbfbf;
  padding-bottom: 1rem;
  color: #fff;
}
.consult-form > input:focus {
  border-color: #ac7e65;
}
.consult-form > input::placeholder {
  color: #fff;
}
.consult-form button {
  width: calc(24% - 2rem);
  display: flex;
  align-items: center;
  gap: 1.4rem;
}
.consult-form button svg {
  width: 3.9rem;
}
.consult-form button:hover {
  color: #00ccff;
}
.consult-form label {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s all;
  cursor: pointer;
  user-select: none;
  gap: 0.5rem;
  font-size: 0.8rem;
}
.consult-form label input {
  border: 1px solid #ac7e65;
  background: transparent;
  appearance: none;
  flex-shrink: 0;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  transition: 0.6s all;
}
.consult-form label input:checked {
  background: #ac7e65;
}
.consult-form label input:checked + span {
  color: #fff;
}
.consult-form label span {
  transition: 0.6s all;
  color: #ccc;
}
.consult-blue {
  color: #e39b25;
  padding-bottom: 7.5rem;
}
.consult-blue .consult-form > input {
  color: #e39b25;
}
.consult-blue .consult-form > input:focus {
  border-color: #e39b25;
}
.consult-blue .consult-form > input::placeholder {
  color: #e39b25;
}
.consult-blue .consult-form label {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s all;
  cursor: pointer;
  user-select: none;
}
.consult-blue .consult-form label input {
  border: 1px solid #e39b25;
}
.consult-blue .consult-form label input:checked {
  background: #e39b25;
}
.consult-blue .consult-form label input:checked + span {
  color: #e39b25;
}
.consult-blue .consult-form label span {
  transition: 0.6s all;
  color: #ccc;
}

.nature {
  padding: 6.5rem 0 11rem;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.nature .section-title {
  text-align: right;
  padding-right: 6rem;
  text-transform: uppercase;
  line-height: 1.75;
  margin-bottom: 6rem;
  color: #fff;
}
.nature .section-title span {
  display: block;
}
.nature .section-title span:nth-child(1) {
  padding-right: 7rem;
}
.nature .section-title span:nth-child(3) {
  padding-right: 10rem;
}
.nature .nav {
  margin-top: 3rem;
  padding-left: calc(100% - 61rem);
  position: relative;
  z-index: 3;
}
.nature-item {
  height: 32.5rem;
  width: 55.5rem;
  margin-left: 1.75rem;
}
.nature-item img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  border-radius: 1.5rem;
}
.nature-leafs img {
  display: block;
  position: absolute;
  z-index: 2;
  pointer-events: none;
}
.nature-leafs img:nth-child(1) {
  width: 34.5rem;
  top: 0;
  left: 0;
}
.nature-leafs img:nth-child(2) {
  width: 44.4rem;
  bottom: 0;
  right: 0;
}
.nature-circles {
  width: 53rem;
  height: 53rem;
  border-radius: 50%;
  border: 1px solid #fff;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -26.5rem;
  opacity: 0.4;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nature-circles span {
  width: 16.3rem;
  height: 16.3rem;
  border-radius: 50%;
  border: 1px solid #fff;
}

.advantages {
  padding: 11.5rem 0 5rem;
  /* background: url(../img/advantages/pattern.png) top left no-repeat; */
  background-size: 70rem;
  position: relative;
}
.advantages__btn {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -5.6rem;
  z-index: 2;
  background: #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #1c263b;
  flex-direction: column;
  height: 11.2rem;
  width: 11.2rem;
  border-radius: 50%;
  fill: #fff;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  gap: 1rem;
  font-size: 1rem;
  text-align: center;
  padding: 2rem;
}
.advantages__btn svg {
  width: 1.35rem;
  height: 1.35rem;
}
.advantages__btn:hover {
  color: #00ccff;
}
.advantages img {
  transition: 0.6s all;
  transform: scaleY(0);
  transform-origin: center top;
}
.advantages img.active {
  transform: none;
}
.advantages-images {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.advantages-images img {
  border-radius: 1.5rem;
  display: block;
}
.advantages-images img:nth-child(1) {
  width: 30.4rem;
  margin-bottom: 3.7rem;
  margin-right: -4.5rem;
  position: relative;
  z-index: 1;
}
.advantages-images img:nth-child(2) {
  width: 23.3rem;
}
.advantages__title {
  display: flex;
  align-items: flex-start;
  font-size: 6rem;
  gap: 3.6rem;
  font-weight: 500;
  color: #e39b25;
}
.advantages__title img {
  width: 17rem;
  border-radius: 1.5rem;
}
.advantages-content {
  position: relative;
  z-index: 1;
  padding: 0 6.5rem;
  margin-top: -3rem;
  display: flex;
  gap: 2rem;
}
.advantages__img {
  width: 50%;
}
.advantages__img img {
  width: 100%;
  display: block;
  border-radius: 1.5rem;
}
.advantages__text {
  font-size: 1.05rem;
  line-height: 1.6;
  width: 50%;
}
.advantages__text p {
  margin-bottom: 1.25rem;
}
.advantages__text p:last-child {
  margin-bottom: 0;
}

.harmony {
  background: linear-gradient(74deg, #4b469a 0%, #803c93 100%);
  color: #fff;
  display: flex;
  position: relative;
}
.harmony-left {
  width: 43%;
  background: #233e99;
  padding: 4.2rem 3.6rem 5.5rem 6rem;
}
.harmony__title {
  margin-top: 3rem;
  line-height: 1.1;
  font-size: 2rem;
  font-weight: 50;
  max-width: 15rem;
}
.harmony-choose {
  border-top: 1px solid #5f72c4;
  display: flex;
  flex-wrap: wrap;
}
.harmony-choose__item {
  width: 50%;
  border-bottom: 1px solid #5f72c4;
  padding: 4rem 0 0.5rem;
  padding-right: 1.1rem;
  font-size: 0.8rem;
  cursor: pointer;
  user-select: none;
}
.harmony-choose__item:nth-child(2n) {
  padding-right: 0;
  padding-left: 1.1rem;
  position: relative;
}
.harmony-choose__item:nth-child(2n)::before {
  content: "";
  height: 85%;
  top: 7.5%;
  width: 1px;
  background: #5f72c4;
  left: 0;
  position: absolute;
}
.harmony-choose__item.current {
  color: #00ccff;
}
.harmony-choose__item.current .harmony-choose__ico {
  border-color: #00ccff;
  color: #00ccff;
}
.harmony-choose__ico {
  width: 100%;
  height: 5.3rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  transition: 0.6s all;
  border: 1px solid transparent;
  margin-bottom: 3rem;
}
.harmony-choose__ico svg {
  width: 3.8rem;
}
.harmony-right {
  width: 57%;
  padding-top: 4rem;
  padding-right: 3.5rem;
}
.harmony-tab {
  display: none;
}
.harmony-tab:first-child {
  display: block;
}
.harmony-tab__img {
  width: 100%;
  height: 22.2rem;
}
.harmony-tab__img img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  border-radius: 0 1.5rem 1.5rem 0;
}
.harmony-tab__name {
  font-size: 7.5rem;
  padding-left: 4rem;
  margin-top: -4.5rem;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  font-weight: 500;
}
.harmony-tab .section-text {
  padding-left: 4rem;
  font-size: 0.9rem;
  max-width: 45rem;
}
.harmony__leaf {
  position: absolute;
  right: 5rem;
  top: 11rem;
  width: 37rem;
  z-index: 2;
  pointer-events: none;
}
.harmony__leaf img {
  display: block;
  width: 100%;
}

.pattern {
  /* background: url(../img/move/pattern.png) center 6rem no-repeat; */
  background-size: 100%;
}

.location {
  padding-top: 6rem;
}
.location .section-title {
  margin-bottom: 3rem;
}
.location__map {
  height: 26rem;
  border-radius: 1.5rem;
  overflow: hidden;
}
.location__map #locationMap {
  width: 100%;
