}
.genplan-item:nth-child(3) {
  left: 49%;
  top: 73%;
}
.genplan-item:nth-child(4) {
  left: 52%;
  top: 58%;
}
.genplan-item:nth-child(5) {
  left: 55%;
  top: 40%;
}
.genplan-item:nth-child(6) {
  left: 73%;
  top: 33%;
}
.genplan-item:nth-child(7) {
  left: 84%;
  top: 57%;
}
.genplan-item:nth-child(8) {
  left: 87%;
  top: 40%;
}
.genplan-item:nth-child(9) {
  left: 71%;
  top: 66%;
}
.genplan-item:nth-child(10) {
  left: 8%;
  top: 59%;
}
.genplan-info {
  opacity: 0;
  visibility: hidden;
