  left: -0.45rem;
  position: absolute;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0.25rem 0.5rem 0.25rem 0;
  border-color: transparent #fff transparent transparent;
  transform: rotate(0deg);
}
.genplan-info.active {
