.building {
  position: relative;
  height: 100dvh;
  z-index: 1;
}
.building::before {
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0) 55.06%,
    rgba(0, 0, 0, 0.6) 100%
  );
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  content: "";
  z-index: 1;
  pointer-events: none;
}
.building-wrap {
  height: 100%;
}
.building-main {
  position: relative;
  width: 100%;
  height: 100%;
}
.building-main img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.building-main svg {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.building-main svg path {
  opacity: 0;
  transition: 0.1s all;
  animation: svgPulse 2s infinite linear;
  fill: #e39b25;
}
.building-main svg path:hover {
  opacity: 1;
  fill: #e39b25;
  animation: none;
}
.building-main svg path:not([data-number]) {
  pointer-events: none;
}

